.aboutUs-banner > img {
  width: 100%; /* El contenedor toma el ancho completo de la celda del grid */
  height: 100%; /* Asegura que la imagen mantenga su proporción */
  object-fit: contain; /* Asegura que la imagen mantenga su proporción */
}

.aboutUs-banner {
  grid-column: span 2; /* Ocupa 2 columnas */
}
